.contactForm {
    color: var(--secondary-text-color);
    padding: 10px;
    border: 3px solid;
    width: 300px;
    border-radius: 4px;
    border-color: var(--secondary-text-color);
}

.contactFormBlock {
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
}

.contactFormItem {
    width: 100%;
    margin-bottom: 5px;

}

.contactFormInput {
    height: 20px;
    border: 2px solid;
    padding: 2px;
    border-radius: 8px;
    border-color: var(--primary-accent-color);
    transition: outline 250ms cubic-bezier(0.4, 0, 0.2, 1), border-color 250ms cubic-bezier(0.4, 0, 0.2, 1), 
}

.contactFormInput:focus {
    outline: none;
    border-color: var(--secondary-text-color);
}

.btnContactForm {
    cursor: pointer;
    padding: 5px 10px;
    border: 2px solid;
    color: var(--secondary-text-color);

    height: 35px;
    font-size: 14px;
    border-radius: 15px;
    transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btnContactForm:hover,
.btnContactForm:focus {
    background: rgb(139, 120, 0);
    color: var(--primary-accent-color);
    border-color: var(--primary-accent-color);
}

.btnContactForm:active {
    background-color: #ffffff;
    transform: translateY(4px);
}