.filterInput {
    margin-bottom: 15px;
    height: 20px;
    border: 2px solid;
    padding: 2px;
    border-radius: 8px;
    border-color: var(--primary-accent-color);
    transition: outline 250ms cubic-bezier(0.4, 0, 0.2, 1), border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
}


.filterInput:focus {
    outline: none;
    border-color: var(--secondary-text-color);
}